.navigation__service {
  display: flex;
  flex-direction: row;
  padding-left: 16px;
  padding-right: 16px; }

.section-table {
  margin: 0px;
  background-color: #F8F8F8; }

.section-table a {
  margin: 16px;
  height: 20px;
  padding-right: 20px;
  width: 300px;
  text-decoration: none;
  color: #1B1C21;
