  margin: 16px; }

@media (min-width: 320px) and (max-width: 767.98px) {
  .swiper-pagination {
    position: relative;
    margin: 20px;
    bottom: 10px; }
  .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    background-color: #7E7E82; }
  .button-sell {
    position: absolute;
    right: 5px;
    bottom: 15px; } }

@media (min-width: 768px) {
  .swiper-pagination {
    display: none; }
  .table__contaner {
    display: block;
    border-radius: 6px; }
  .table__head--visible {
    display: none; }
  .table {
    display: flex;
    width: 100%;
    height: auto;
    padding: 16px; }
  .table__row {
    display: inline;
    margin: 5px; }
  .table__cell--descopt--hide {
    visibility: hidden;
    background-color: #7E7E82; }
  .table__descopt {
    height: 60px;
    background-color: #F8F8F8;
    border: none; }
  .button-sell {
    position: absolute;
    right: 5px;
    bottom: 5px; } }

.navigation ul {
  list-style: none; }

.navigation span {
  visibility: hidden; }

.navigation li {
  width: 40px;
  height: 40px;
  margin-right: 8px; }

.navigation__basic {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
  padding-left: 16px;
  padding-right: 16px; }

.navigation__status {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: end;
  padding-left: 16px;
  padding-right: 16px; }

.navigation__burger {
  background: #FF3E79 url(./static/burger.svg) 50% 50% no-repeat; }

.navigation__group {
  padding: 0px 0px; }

.navigation__call {
  background: #FF3E79 url(./static/call.svg) 50% 50% no-repeat; }

.navigation__chat {
  background: #FF3E79 url(./static/chat.svg) 50% 50% no-repeat; }

.navigation__profile {
  background: #FF3E79 url(./static/profile.svg) 50% 50% no-repeat; }

.navigation__repair {
  background: #FF3E79 url(./static/repair.svg) 50% 50% no-repeat;
  border: none; }

.navigation__checkstatus {
  background: #FF3E79 url(./static/checkstatus.svg) 50% 50% no-repeat; }

.menu-body {
  width: 320px;
  min-height: 100%;
  top: 0;
  left: 0;
  box-shadow: -2px 0px 4px rgb #454f7e, 16px 0px 52px #0e1450;
  position: fixed;
  float: left; }

.header {
  width: 100%;
  list-style-type: none; }

.header__menu {
  list-style-type: none;
  display: flex;
  padding: 0;
  margin-left: 16px; }

.header__menu span {
  visibility: hidden; }

.button__close {
  background: #FF3E79 url(./static/close.svg) 50% 50% no-repeat;
  padding: 16px; }

.header__group {
  background: url(./static/Group.svg) no-repeat; }

.header__search {
  background: #FF3E79 url(./static/search.svg) 50% 50% no-repeat;
  position: relative;
  right: -24px; }

.nav {
  list-style-type: none;
  padding: 0; }

.nav li {
  font-size: 16px;
  line-height: 24px;
  margin: 0;
  padding: 16px 0px; }

.nav__link {
  text-decoration: none;
  color: #1B1C21;
  padding-left: 24px; }

.nav__link:hover {
  color: #7E7E82;
  border-left: 4px solid #41F6D7;
  box-sizing: border-box; }

.navigation--position {
  list-style-type: none;
  position: relative;
  bottom: -5%;
  border: none;
  visibility: visible; }

.navigation--position span {
  opacity: 0; }

.footer__menu {
  position: relative;
  bottom: -5%;
  left: 20px; }

.footer__contacts {
  margin-bottom: 10px; }

.footer__contacts a {
  display: block;
  text-decoration: none;
  color: #1B1C21; }

.footer__phone {
  font-size: 24px;
  line-height: 32px;
  font-weight: bold; }

.footer__langueges {
  display: flex;
  list-style-type: none;
  padding: 0; }

.lang {
  margin-right: 7px;
  border-style: none;
  background-color: inherit;
  font-weight: bold; }

.footer__lang--active {
  color: #7E7E82; }

.lang--checked {
  color: red;
  border: 1px solid green; }

.footer__langueges .lang:hover {
  opacity: 0.3;
  cursor: pointer; }

.modal {
  display: none;
  position: absolute; }

.menu--show {
  display: block;
  z-index: 1;
  position: fixed; }

.modal--show {
  display: block;
  position: absolute;
  z-index: 1000;
  right: 0; }

.main-body--style {
  position: relative;
  left: 320px;
  transition: left 3s; }

@-webkit-keyframes button-style {
  100% {
    opacity: 0.5;
    transform: scale(0.8); } }

@keyframes button-style {
  100% {
    opacity: 0.5;
    transform: scale(0.8); } }

.button-styles:hover {
  -webkit-animation-name: button-style;
          animation-name: button-style;
  -webkit-animation-duration: 200ms;
          animation-duration: 200ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

@media (min-width: 320px) and (max-width: 549.98px) {
  .navigation {
    display: grid;
    grid-template-columns: 1fr  0px 132px;
    border-bottom: 1px solid #41F6D7; }
  .navigation__service {
    visibility: hidden; }
  .navigation {
    display: grid;
    grid-template-columns: 1fr 200px 150px;
    border-bottom: 2px solid #41F6D7; }
  .navigation__burger {
    border-right: 1px solid #EAEAEA; }
  .navigation__service {
    border-right: 2px solid #EAEAEA;
    padding-right: 16px; }
  .main-about {
    display: block;
    width: 100%;
    height: auto;
    border: 1px solid green; }
  .main__text p {
    width: auto;
    height: 50px;
    overflow: hidden;
    margin: 16px; }
  .main__img-about {
    margin: 20px;
    width: 100%; }
  .main__text {
    width: auto;
    overflow: hidden;
    margin: 16px; }
  .main__text--show p {
    height: auto;
    overflow: visible; }
  .read__more {
    background-image: url(/src/img/icon_hide.svg) 10px 5px; } }

@media (min-width: 550px) and (max-width: 1119px) {
  .navigation {
    display: grid;
    grid-template-columns: 1fr 200px 150px;
    border-bottom: 2px solid #41F6D7; }
  .navigation__burger {
    border-right: 1px solid #EAEAEA; }
  .navigation__service {
    border-right: 2px solid #EAEAEA;
    padding-right: 16px; }
  .main-about {
    display: flex;
    width: 100%; }
  .main__text p {
    width: 80%;
    margin: 16px;
    height: 50px;
    overflow: hidden; }
  .main__img-about {
    margin-top: 20px;
    width: 100%;
    background-size: contain; }
  .main__text--show p {
    height: auto;
    overflow: visible; }
  .read__more {
    background-image: url(/src/img/icon_hide.svg) 10px 5px; } }

@media (min-width: 1120px) {
  .navigation {
    display: grid;
    grid-template-columns: 0 0 300px;
    float: right;
    width: 70px;
    height: 40px;
    margin: 0; }
  .main {
    float: left; }
  .navigation__basic, .navigation__service {
    display: none; }
  .navigation__status span {
    visibility: visible;
    position: relative;
    right: 150px; }
  .navigation__repair,
  .navigation__checkstatus {
    text-align: center;
    color: #7E7E82;
    line-height: 40px;
    white-space: nowrap;
    padding: 0 10px;
    font-size: 16px;
    font-weight: bold; }
  .navigation__repair {
    position: relative;
    left: -150px; }
  .main__slider {
    overflow: auto; }
  .main-about {
    display: flex;
    width: 100%;
    height: 300px; }
  .main__text {
    width: 100%;
    height: auto;
    float: left; }
  .main__read-more {
    visibility: hidden; }
  .main__img-about {
    width: 80%; } }

@font-face {
  font-family: "TTLakes-Thin";
  src: url(./fonts/TTLakes-Thin.ttf) format("ttf"), url(./fonts/TTLakes-Thin.eot) format("eot");
  font-style: normal;
  font-weight: normal; }

body {
